<template>
    <div class="employee">
        <div class="employee__block">
            <EmployeeItem @find="find" v-for="employee in EMPLOYEE" :key="employee.article"
                v-bind:employee_data="employee" />
        </div>
        <div class="employee__block2 ">
            <!-- <li v-for="el in staff" :key="el.id">
                <a href="">{{el.name}}</a>
            </li> -->
            <Emp  v-bind:staff="staff"/>

        </div>
    </div>
</template>
<script>
    import EmployeeItem from '@/components/employee-item'
    import Emp from '@/components/emp'
    import {
        mapActions,
        mapGetters
    } from 'vuex'

    export default {
        name: 'employee',
        data() {
            return {
                staff: []
            }
        },
        components: {
            EmployeeItem,
            Emp
        },
        computed: {
            ...mapGetters([
                // this.$store.state.employee
                'EMPLOYEE'
            ]),
            // ppp() {
            //     return this.$store.getters.findById(this.params)
            // }
        },
        methods: {
            ...mapActions([
                'GET_EMPLOYEE',
            ]),
            find(employee_data) {
                this.staff.splice(employee_data,1)
                this.staff.push({
                    _id: employee_data._id,
                    article: employee_data.article,
                    image: employee_data.image,
                    name: employee_data.name,
                    surname: employee_data.surname,
                    patronymic: employee_data.patronymic,
                    position: employee_data.position,
                    description: employee_data.description
                })

            },
            // removestaff(index) {
            //     this.staff.splice(index,1)
            //     // @removestaff="removestaff"
            // },
            // onObj(data) {
            //     console.log('Тот самый сотрудник', data)
            // }
        },
        mounted() {
            this.GET_EMPLOYEE()
                .then((response) => {
                    if (response.data) {
                        console.log('Data arrived')
                    }
                })
                .catch((error) => {
                    console.log(error)
                })
        }
    }
</script>